body {
  color: #343434;
  font-family: sans-serif;
}

.container {
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
}

.breed-select {
  border: 1px solid #343434;
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 20px;
}

.loader-container {
  display: block;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader-text {
  margin-left: 10px;
  display: block;
}

.cat-info {
  grid-template-columns: 1fr 2fr;
  align-items: flex-start;
  gap: 20px;
  display: grid;
}

